import React, { useContext, useState } from "react";
import { StoreContext } from "../../../store/StoreProvider";

const AddToCartButton = (id) => {
  const { items, cartStatus, setCartStatus } = useContext(StoreContext);

  const isAlreadyInCart = cartStatus.filter((i) => {
    if (i.id == id) return i;
  });

  const handleClickButton = (e) => {
    const itemId = e.target.getAttribute("data-value");
    const selectedItem = items.filter((i) => (itemId == i.id ? i : null));

    // const isAlreadyInCart = cartStatus.filter((i) => {
    //   if (i.id == id) return i;
    // });

    if (isAlreadyInCart.length > 0) {
      const itemIndex = cartStatus.findIndex((i) => id === i.id);
      let newArray = [...cartStatus];
      newArray.splice(itemIndex, 1);
      setCartStatus(newArray);
      return;
    } else {
      console.log("dac tosta ze dodano do wozka");
      setCartStatus(cartStatus.concat(selectedItem));
    }
  };

  return (
    <button
      className={isAlreadyInCart.length ? "btn btn-danger" : "btn btn-success"}
      data-value={id}
      onClick={handleClickButton}
    >
      {/* <i class="fa fa-cart-plus"></i> */}
      {cartStatus.findIndex((i) => id === i.id) === -1 ? (
        // ? "Do koszyka"
        <i data-value={id} class="fa fa-cart-plus"></i>
      ) : (
        <i class="fa fa-eraser"></i>
      )}
    </button>
  );
};

export default AddToCartButton;
