import React, { useContext, useEffect } from "react";
import { StoreContext } from "../../../store/StoreProvider";

const Success = () => {
  const { setCheckoutStep, orders } = useContext(StoreContext);
  useEffect(() => {
    window.scrollTo(0, 0);
    setCheckoutStep(4);
  }, []);
  console.log(orders);
  return (
    <div>
      <p>Gratulacje</p>
      <p>Szczegóły zamówienia znajdziesz w zakładce 'Twoje konto'</p>
    </div>
  );
};

export default Success;
